@use "global" as *;

.mask-slide {
    position: relative;
    // width: 80%;
    // margin: 30px auto 30px;
}

.mask {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: #408F95;
}

.mask-slide img {
    display: block;
    width: 100%;
}