.button {
  font-family: "Gotu", sans-serif;
  display: inline-block;
  padding-left: 2.375rem;
  padding-right: 5.5rem;
  padding-block: 1.1875rem;
  border: solid 1px #408F95;
  line-height: 1;
  color: #ffffff;
  margin-right: 0.1875rem;
  position: relative;
  transition: all 0.3s;
  font-size: 0.875rem;
  margin-bottom: 0.375rem;
  z-index: 2;
  cursor: pointer;
  background-color: #408F95;
}

.button.button--send {
  padding-left: 3.4375rem;
  padding-right: 7rem;
  padding-top: 1.3125rem;
  padding-bottom: 1.1875rem;
}

.button.button--send::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 12.625rem;
  height: 3.4375rem;
  top: 0;
  left: 0;
  margin-top: 0.25rem;
  margin-left: 0.25rem;
  box-shadow: 0 0 0 1px #408F95;
  z-index: -2;
}

.button::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-origin: left top;
  transform: scale(0, 1);
  background-color: #ffffff;
  transition: all 0.3s;
  z-index: -1;
}

.button::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 12.625rem;
  height: 3.3125rem;
  top: 0.1875rem;
  left: 0;
  margin-top: 0;
  margin-left: 0.125rem;
  box-shadow: 0 0 0 1px #408F95;
  z-index: -2;
}

.button span {
  font-family: "Gotu", sans-serif;
}

.arrow {
  line-height: 1;
  position: relative;
}

/* 右向き */
.arrow::after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 7px;
  border-right: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  transform: skew(45deg);
  top: 0.375rem;
  left: 0.3125rem;
}

/* 右向き */
.arrow--contact::after {
  left: 0.8125rem;
  top: 0.3125rem;
}

/* .hover
-------------------------------------------------------------*/
.button:hover {
  color: #408F95;
}

.button:hover::before {
  transform: scale(1, 1);
}

.button:hover .arrow::after {
  border-right: 1px solid #408F95;
  border-bottom: 1px solid #408F95;
}

/* button--white
-------------------------------------------------------------*/
.button--white {
  padding-left: 2.5rem;
  padding-right: 5.9375rem;
  padding-block: 1.25rem;
  color: #408F95;
  background-color: #fff;
  position: relative;
}

.button--white::before {
  background-color: #408F95;
}

.button.button--white::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 12.5rem;
  height: 3.4375rem;
  top: 0;
  left: 0;
  margin-top: 0.25rem;
  margin-left: 0.25rem;
  box-shadow: 0 0 0 1px #fff;
  z-index: -2;
}

.arrow--white {
  line-height: 1;
  position: relative;
}

/* 右向き */
.arrow--white::after {
  content: "";
  position: absolute;
  width: 2.5rem;
  height: 7px;
  border-right: 1px solid #408F95;
  border-bottom: 1px solid #408F95;
  transform: skew(45deg);
  top: 0.375rem;
  left: 0.75rem;
}

.button--white:hover {
  color: #fff;
  background-color: #408F95;
  border: solid 1px #fff;
}

.button--white:hover::before {
  transform: scale(1, 1);
}

.button--white:hover .arrow--white::after {
  border-right: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

