/* .loading
-------------------------------------------------------------*/
.fv-loading {
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  animation: out 3s forwards;
  z-index: 18;
}

@keyframes out {
  0%, 80% {
    opacity: 1;
    visibility: visible;
    background-color: #fff;
  }
  90%, 100% {
    opacity: 0;
    visibility: hidden;
    display: none;
    background-color: transparent;
  }
}
.fv-loading__container {
  margin: 0;
  padding: 0;
  height: 100vh;
  position: relative;
  height: inherit;
  width: 100%;
  display: flex;
  overflow: hidden;
  opacity: 0;
  background-color: transparent;
  animation: containerFade 3s forwards;
  /* 新しいアニメーション */
}

@keyframes containerFade {
  0%, 30% {
    opacity: 0;
  }
  40%, 100% {
    opacity: 1;
  }
}
.fv-loading__split-left,
.fv-loading__split-right {
  position: relative;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.fv-loading__split-left img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transform: translateY(100%);
}

.fv-loading__split-right img {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transform: translateY(100%);
}

.fv-loading__split-left img.active {
  animation: slideIn 0.8s ease-in-out forwards;
  animation-delay: 0.8s;
}

.fv-loading__split-right img.active {
  animation: slideIn 0.8s ease-in-out forwards;
  animation-delay: 0.9s;
}

@keyframes slideIn {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
