@use "global" as *;

.campaign-section-cards {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    row-gap: rem(40);
    max-width: rem(280);

    @include mq('md') {
        grid-template-columns: repeat(2, 1fr);
        column-gap: rem(40);
        row-gap: rem(80);
        max-width: rem(1080);
    }
}